.labelContainer {
    display: block;
    position: relative;
    padding-left: 35px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 15px;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    text-align: left;
}

/* Hide the browser's default radio button */
.labelContainer input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}

/* Create a custom radio button */
.checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: #eee;
    border-radius: 50%;
    z-index: 1;
}

.checked {
    background-color:  rgba(191, 4, 4);
    z-index: 1;
}

.notChecked {
    background-color: #eee;
}

 /*On mouse-over, add a grey background color*/ 
.labelContainer:hover input ~ .checkmark {
    background-color:  rgba(191, 4, 4);
    z-index: 1;
  }
  

/* Show the indicator (dot/circle) when checked */
.labelContainer input:checked ~ .checkmark:after {
    display: block;
    background-color:   rgba(191, 4, 4);
}

/* Style the indicator (dot/circle) */
.labelContainer .checkmark:after {
    top: 9px;
    left: 9px;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: blue;
}


